import { Component, OnInit } from '@angular/core';
import { Waypoint } from '../types';
import { DataService } from '../data.service';
import * as _ from 'underscore';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import {map, startWith} from 'rxjs/operators';

@Component({
  selector: 'app-planner',
  templateUrl: './planner.component.html',
  styleUrls: ['./planner.component.css']
})
export class PlannerComponent implements OnInit {

  constructor(private ds:DataService) { }
  wpts:Waypoint[] = [];
  list:Waypoint[];
  selectedWpt:string = "-1";
  format:string = "secs";


  waypointType:string = "db";

  diaName:string = "";
  diaLat:string = "N";
  diaLatD:number = null;
  diaLatM:number = null;

  diaLong:string = "E";
  diaLongD:number = null;
  diaLongM:number = null;

  Tacan:string = "";
  TacanCh:string = "";
  Radial:number = null;
  Dme:number = null;

  dialogOpen:boolean = false;

  myControl = new FormControl();

  filteredWpts:Observable<Waypoint[]>;


  exportFormat:string = "csv";

  ngOnInit() {
    this.wpts = this.ds.planningCache;
    (async () => {
      this.list = await this.ds.getWaypoints();
      this.filteredWpts = this.myControl.valueChanges.pipe(
        startWith<string | Waypoint>(''),
        map(value => typeof value == 'string' ? value : value.Name),
        map(name => name ? this.filter(name) : this.list.slice())
      );
      console.log(this.list);
    })();
  }

  filter(name:string):Waypoint[]{
    return this.list.filter(wpt => wpt.Name.toLowerCase().indexOf(name.toLowerCase()) == 0);
  }

  displayFn(wpt?: Waypoint): string | undefined {
    return wpt ? wpt.Name : undefined;
  }

  async addNew(){
    this.myControl.setValue({});
    this.diaName = "";
    this.diaLat = "N";
    this.diaLatD = null;
    this.diaLatM = null;
    this.diaLong = "E";
    this.diaLongM = null;
    this.diaLongD = null;
    this.Tacan = "";
    this.TacanCh = "";
    this.Radial = null;
    this.Dme = null;

    this.dialogOpen = true;
  }

  dialogOk(){
    if(this.waypointType == "custom"){
      let lt = this.diaLatD;
      lt += (this.diaLatM / 60);
      if(this.diaLat == "S"){
        lt = lt * -1;
      }
  
      let ll = this.diaLongD;
      ll += (this.diaLongM / 60);
      if(this.diaLong == "W"){
        ll = ll * -1;
      }
      let newWP:Waypoint = {
        Id: -1,
        Name: this.diaName,
        Tacan: this.Tacan,
        TacanChl: this.TacanCh,
        Radial: this.Radial,
        DME: this.Dme,
        Latitude: lt,
        Longitude: ll
      };
      this.ds.getCoordinateFormats(newWP);
      this.wpts.push(newWP);
      this.ds.planningCache = this.wpts;
    } else {
      this.wpts.push(this.myControl.value);
      this.ds.planningCache = this.wpts;
    }

    this.selectedWpt = "-1";
    this.dialogOpen = false;
  }
  dialogCancel(){
    this.selectedWpt = "-1";
    this.dialogOpen = false;
  }

  remove(i:number){
    this.wpts.splice(i, 1);
    this.ds.planningCache = this.wpts;
  }


  getLatStr(w:Waypoint){
    if(this.format == "secs")
      return w.LatitudeSecondsString;
    else if(this.format == "decs")
      return w.LatitudeDecimalStr;
    else 
      return w.Latitude.toFixed(5);
  }
  getLongStr(w:Waypoint){
    if(this.format == "secs")
      return w.LongitudeSecondsString;
    else if(this.format == "decs")
      return w.LongitudeDecimalStr;
    else 
      return w.Longitude.toFixed(5);
  }

  getDistance(w:Waypoint, i:number){
    let prev = this.wpts[i - 1];
    if(prev != null){
      return this.distance(w.Latitude, w.Longitude, prev.Latitude, prev.Longitude, "N");
    } else {
      return "";
    }
  }
  getHeading(w:Waypoint, i:number){
    let prev = this.wpts[i - 1];
    if(prev != null){
      return this.bearing(prev.Latitude, prev.Longitude, w.Latitude, w.Longitude);
    } else {
      return "";
    }
  }

  distance(lat1, lon1, lat2, lon2, unit) {
    if(!lat1 || !lat2 || !lon1 || !lon2)
      return "";
    var radlat1 = Math.PI * lat1/180
    var radlat2 = Math.PI * lat2/180
    var theta = lon1-lon2
    var radtheta = Math.PI * theta/180
    var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
    dist = Math.acos(dist)
    dist = dist * 180/Math.PI
    dist = dist * 60 * 1.1515
    if (unit=="K") { dist = dist * 1.609344 }
    if (unit=="N") { dist = dist * 0.8684 }
    return dist.toFixed(1);
  }

  _toRad(n) {
    return n * Math.PI / 180;
  }
  _toDeg(n) {
    return n * 180 / Math.PI;
  }
  
  bearing(lat1,lng1,lat2,lng2) {
    if(!lat1 || !lat2 || !lng1 || !lng2)
      return "";
    let rlat1 = this._toRad(lat1);
    let rlng1 = this._toRad(lng1);
    let rlat2 = this._toRad(lat2);
    let rlng2 = this._toRad(lng2);

    let y = Math.sin(rlng2 - rlng1) * Math.cos(rlat2);
    let x = Math.cos(rlat1) * Math.sin(rlat2) - 
              Math.sin(rlat1) * Math.cos(rlat2) * Math.cos(rlng2 - rlng1);
    
    let rhdg = Math.atan2(y, x);
    let hdg = this._toDeg(rhdg);
    return ((hdg + 360) % 360).toFixed(0);
  }

  export(){
    switch(this.exportFormat){
      case "csv":
        this.exportCsv();
        break;

      case "cdu":
        this.exportCdu();
        break;

      default:
        break;
    }
  }

  exportCsv(){
    let csv = 'sep=;\n';
    csv += "#;Name;Latitude;Longitude;Tacan;Channel;Radial;DME;Distance;Heading;Alt;Speed;DTOT";
    let index = 0;
    for(let w of this.wpts){
      let prev = this.wpts[index - 1];
      let dist = this.getDistance(w, index);
      let hdg = this.getHeading(w, index);
      let alt = w.Altitude ? w.Altitude.toString() : "";
      let spd = w.Speed ? w.Speed.toString() : "";
      let tot = w.DTOT ? w.DTOT.toString() : "";
      csv += `\n${index};${w.Name};${this.getLatStr(w).replace('°', '')};${this.getLongStr(w).replace('°', '')};${w.Tacan};${w.TacanChl};${w.Radial};${w.DME};${dist};${hdg};${alt};${spd};${tot}`;
      index++;
    }
    console.log(csv);
    this.saveString(csv, 'text/csv', 'export.csv');
  }
  exportCdu(){
    console.log("Export CDU")
    let txt = "";
    for(let w of this.wpts){
      let ltdz = w.LatD < 10 ? "0" : "";
      let ltmz = w.LatM < 10 ? "0" : "";
      let lodz = w.LongD < 10 ? "0" : "";
      let lomz = w.LongM < 10 ? "0" : "";
      let alt = w.Altitude ? w.Altitude.toString() : "00000";
      let tot = w.DTOT ? w.DTOT : "000000";
      
      while(alt.length < 5){
        alt = "0" + alt;
      }

      txt += `${w.LatDir}${ltdz}${w.LatD}${ltmz}${w.LatM.toFixed(3).replace('.', '')} ${w.LongDir}${lodz}${w.LongD}${lomz}${w.LongM.toFixed(3).replace('.', '')} EL${alt} T${tot} ${w.Name}\r\n`;
    }
    console.log(txt);
    this.saveString(txt, 'text/plain', 'cdu-export.txt');
  }

  saveString(data:string, mime:string, fileName:string){
    var hiddenElement = document.createElement('a');
    hiddenElement.href = 'data:' + mime + ';charset=utf-8,' + encodeURI(data);
    hiddenElement.target = '_blank';
    hiddenElement.download = fileName;
    hiddenElement.click();
  }
}
