import { Injectable } from '@angular/core';
import { Waypoint } from './types';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  mock = [
    {
      Id: 1,
      Name: 'FYTTR',
      Latitude: 36.5124,
      Longitude: -115.12523,
      Tacan: 'LSV',
      Radial: 267,
      dme: 33,
      TacanChl: '12X'
    },
    {
      Id: 2,
      Name: 'JAYSN',
      Latitude: 36.57083333,
      Longitude: -116.03777778,
      Tacan: 'BTY',
      Radial: 95,
      dme: 37,
      TacanChl: '76X'
    },
    {
      Id: 3,
      Name: 'JAYSN-Fake',
      Latitude: 36.57083333,
      Longitude: 116.03777778,
      Tacan: 'BTY',
      Radial: 95,
      dme: 37,
      TacanChl: '76X'
    }
  ];
  planningCache:Waypoint[] = [];


  constructor(private http:HttpClient) { }

  async getWaypoints():Promise<Waypoint[]>{
    let wpts:Waypoint[] = await this.http.get('/api/waypoints/getall').toPromise() as Waypoint[];
    for(let w of wpts){
      this.getCoordinateFormats(w);
    }
    return wpts as Waypoint[];
    // return this.mock;
  }

  async addWaypoint(wp:Waypoint){
    let item = this.http.post('/api/waypoints', wp).toPromise();
    return item;
  }
  async updateWaypoint(wp:Waypoint){
    let item = this.http.put('/api/waypoints/' + wp.Id, wp).toPromise();
    return item;
  }

  getCoordinateFormats(wpt:Waypoint):Waypoint {
    //Latitude
    let str = "";
    let dir = "N";
    wpt.Tacan = wpt.Tacan ? wpt.Tacan : "";
    wpt.TacanChl = wpt.TacanChl ? wpt.TacanChl : "";
    wpt.Radial = wpt.Radial ? wpt.Radial : 0;
    wpt.DME = wpt.DME ? wpt.DME : 0;
    let zeroedValue = Math.abs(wpt.Latitude);
    if(wpt.Latitude < 0){
      dir = "S";
    }
    let deg = parseInt(zeroedValue as any);
    let mins = (zeroedValue - deg) * 60;
    let minZero = mins < 10 ? "0" : "";
    let secs = (mins - Math.floor(mins)) * 60
    let secZero = secs < 10 ? "0" : "";
    wpt.LatDir = dir;
    wpt.LatD = deg;
    wpt.LatM = mins;
    wpt.LatitudeDecimalStr = `${dir} ${deg}° ${minZero}${mins.toFixed(2)}'`;
    wpt.LatitudeSecondsString = `${dir} ${deg}° ${minZero}${mins.toFixed(0)}' ${secZero}${secs.toFixed(0)}''`;

    //Logitude
    let strLong = "";
    let dirLong = "E";
    let zeroedLong = Math.abs(wpt.Longitude);
    if(wpt.Longitude < 0){
      dirLong = "W";
    }
    let degLong = parseInt(zeroedLong as any);
    let minsLong = (zeroedLong - degLong) * 60;
    let minZeroLong = minsLong < 10 ? "0" : "";
    let secsLong = (minsLong - Math.floor(minsLong)) * 60;
    let secZeroLong = secsLong < 10 ? "0" : "";
    wpt.LongDir = dirLong;
    wpt.LongD = degLong;
    wpt.LongM = minsLong;
    wpt.LongitudeDecimalStr = `${dirLong} ${degLong}° ${minZeroLong}${minsLong.toFixed(2)}'`;
    wpt.LongitudeSecondsString = `${dirLong} ${degLong}° ${minZeroLong}${minsLong.toFixed(0)}' ${secZeroLong}${secsLong.toFixed(0)}''`;

    return wpt;
  }
}
