import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'
import { FormsModule, ReactiveFormsModule } from '@angular/forms'
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { HttpClientModule } from '@angular/common/http';



import {MatRadioModule, MatFormFieldModule, MatInputModule, MatAutocompleteModule, MatSelectModule} from '@angular/material';


import { AppComponent } from './app.component';
import { NavigationComponent } from './navigation/navigation.component';
import { WaypointsComponent } from './waypoints/waypoints.component';
import { PlannerComponent } from './planner/planner.component';
import { DataService } from './data.service';

const routes:Routes = [
  {
    path: '',
    component: PlannerComponent
  },
  {
    path: 'waypoints',
    component: WaypointsComponent
  },
  {
    path: '**',
    redirectTo: ''
  }
]

@NgModule({
  declarations: [
    AppComponent,
    NavigationComponent,
    WaypointsComponent,
    PlannerComponent
  ],
  imports: [
    MatSelectModule,
    MatAutocompleteModule,
    ReactiveFormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatInputModule,
    MatFormFieldModule,
    FormsModule,
    BrowserModule,
    MatRadioModule,
    RouterModule.forRoot(routes, { useHash: true })
  ],
  providers: [DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }
