import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Waypoint } from '../types';

@Component({
  selector: 'app-waypoints',
  templateUrl: './waypoints.component.html',
  styleUrls: ['./waypoints.component.css']
})
export class WaypointsComponent implements OnInit {

  constructor(private ds:DataService) { }
  wpts:Waypoint[] = [];
  cache:Waypoint[];
  format:string = "secs";
  search:string;

  diaId:number = -1;
  dialogOpen:boolean = false;
  diaName:string = "";
  diaLat:string = "N";
  diaLatD:number = null;
  diaLatM:number = null;

  diaLong:string = "E";
  diaLongD:number = null;
  diaLongM:number = null;

  Tacan:string = "";
  TacanCh:string = "";
  Radial:number = null;
  Dme:number = null;

  favoriteSeason: string;

  seasons = [
    'Winter',
    'Spring',
    'Summer',
    'Autumn',
  ];


  ngOnInit() {
    this.load();
  }
  async load(){
    this.wpts = await this.ds.getWaypoints();
    this.cache = JSON.parse(JSON.stringify(this.wpts));
    console.log(this.wpts);
  }

  openDialog(){
    this.diaId = -1;
    this.diaName = "";
    this.diaLat = "N";
    this.diaLatD = null;
    this.diaLatM = null;
    this.diaLong = "E";
    this.diaLongM = null;
    this.diaLongD = null;
    this.Tacan = "";
    this.TacanCh = "";
    this.Radial = null;
    this.Dme = null;
    this.dialogOpen = true;
  }
  openEdit(wpt:Waypoint){
    this.diaId = wpt.Id;
    this.diaName = wpt.Name;
    this.diaLat = wpt.LatDir;
    this.diaLatD = wpt.LatD;
    this.diaLatM = wpt.LatM;
    this.diaLong = wpt.LongDir;
    this.diaLongM = wpt.LongM;
    this.diaLongD = wpt.LongD;
    this.Tacan = wpt.Tacan;
    this.TacanCh = wpt.TacanChl;
    this.Radial = wpt.Radial;
    this.Dme = wpt.DME;
    this.dialogOpen = true;
  }

  async dialogOk(){
    let valid = true;
    let msg ="";
    if(typeof(this.diaLatD) != "number"){
      valid = false;
      msg += "Latitude Degrees must be a number \n";
    }
    if(typeof(this.diaLatM) != "number"){
      valid = false;
      msg += "Latitude Minutes must be a number \n";
    }
    if(typeof(this.diaLongD) != "number"){
      valid = false;
      msg += "Longitude Degrees must be a number \n";
    }
    if(typeof(this.diaLongM) != "number"){
      valid = false;
      msg += "Longitude Minutes must be a number \n";
    }
    if(this.diaLatD > 90 || this.diaLatD < 0){
      valid = false;
      msg += "Latitude degrees not valid \n";
    }
    if(this.diaLatM >= 60  || this.diaLatM < 0){
      valid = false;
      msg += "Latitude minutes not valid \n";
    }
    if(this.diaLongD > 180 || this.diaLongD < 0){
      valid = false;
      msg += "Longitude degrees not valid \n";
    }
    if(this.diaLongM >= 60  || this.diaLongM < 0){
      valid = false;
      msg += "Longitude minutes not valid \n";
    }
    if(this.Radial >= 360  || this.diaLongM < 0){
      valid = false;
      msg += "Radial not valid \n";
    }
    if(this.Dme < 0){
      valid = false;
      msg += "DME value not valid \n";
    }


    if(valid){
      let lt = this.diaLatD;
      lt += (this.diaLatM / 60);
      if(this.diaLat == "S"){
        lt = lt * -1;
      }
  
      let ll = this.diaLongD;
      ll += (this.diaLongM / 60);
      if(this.diaLong == "W"){
        ll = ll * -1;
      }
      let newWP:Waypoint = {
        Id: this.diaId,
        Name: this.diaName,
        Tacan: this.Tacan,
        TacanChl: this.TacanCh,
        Radial: this.Radial,
        DME: this.Dme,
        Latitude: lt,
        Longitude: ll
      };
      if(newWP.Id < 1){
        await this.ds.addWaypoint(newWP);
      } else {
        let newItem = await this.ds.updateWaypoint(newWP);
        console.log(newItem);
      }
      this.load();
      this.diaId = -1;
      this.dialogOpen = false;
    } else {
      alert(msg);
    }
  }
  dialogCancel(){
    this.dialogOpen = false;
  }

  getLatStr(w:Waypoint){
    if(this.format == "secs")
      return w.LatitudeSecondsString;
    else if(this.format == "decs")
      return w.LatitudeDecimalStr;
    else 
      return w.Latitude.toFixed(5);
  }
  getLongStr(w:Waypoint){
    if(this.format == "secs")
      return w.LongitudeSecondsString;
    else if(this.format == "decs")
      return w.LongitudeDecimalStr;
    else 
      return w.Longitude.toFixed(5);
  }

  onSearch(ev:any){
    let newWpts = [];
    for(let w of this.cache){
      let name = w.Name ? w.Name.toLowerCase() : "";
      if(name.indexOf(this.search.toLowerCase()) != -1){
        newWpts.push(w);
      }
    }
    this.wpts = newWpts;
  }
}
